import Round from "./Round";

export class Player {

    chips: number;
    playerBet: number;
    bankerBet: number;
    tieBet: number;
    playerPairBet: number;
    bankerPairBet: number;
    currentRound: Round;

    constructor() {
            this.chips = 1000;
            this.playerBet = 0;
            this.bankerBet = 0;
            this.tieBet = 0;
            this.playerPairBet = 0;
            this.bankerPairBet = 0;
    }

    resetPlayer() {
        this.chips = 1000;
        this.playerBet = 0;
        this.bankerBet = 0;
        this.tieBet = 0;
        this.playerPairBet = 0;
        this.bankerPairBet = 0;
    }
}

export default Player;


